.contact__container{
    grid-template-columns: repeat(2,max-content);
    justify-content: center;
    column-gap: 6rem;
    padding-bottom: 3rem;
}

.contact__title{
    text-align: center;
    font-size: var(--h3-font-size);
    font-weight: var(--font-medium);
    margin-bottom: var(--mb-1-5);
}

.contact__info{
    display: grid;
    row-gap: 1.5rem;
    grid-template-columns: 300px;
}
.contact__card{
    background-color: rgb(249, 249, 249);
    /* border: 1.5px solid rgb(158, 158, 158); */
    box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.22);
    padding: 1rem;
    border-radius: 1.25rem;
    text-align: center;
    margin-top: 2rem;
}
.contact__card-icon{
    font-size: 2rem;
    color: var(--title-color);
    margin-bottom: var(--mb-0-25);
}
.contact__card-title,
.contact__card-data{
    font-size: var(--small-font-size);
}
.contact__card-title{
    font-weight: var(--font-medium);
}
.contact__card-data{
    display: block;
    margin-bottom: var(--mb-0-75);
}
.contact__button{
    color: var(--title-color);
    font-size: var(--small-font-size);
    display: inline-flex;
    align-items: center;
    justify-content: center;
    column-gap: 0.25rem;
}
.contact__button-icon{
    font-size: 1rem;
    transition: 0.3s;
}
.contact__button:hover .contact__button-icon{
    transform: translate(0.25rem);
}
.contact__form{
    width: 360px;
}
.contact__form-div{
    position: relative;
    z-index: 0;
    margin-bottom: var(--mb-2);
    height: 5rem;
}
.contact__form-input{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 1px solid rgb(249, 249, 249);
    box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.22);
    background-color: rgb(249, 249, 249);
    color: var(--text-color);
    outline: none;
    border-radius: 1.25rem;
    padding: 1.5rem;
    z-index: 1;
}
.contact__form-tag{
    position: absolute;
    top: -0.75rem;
    left: 1.25rem;
    font-size: var(--smaller-font-size);
    /* padding: 0.25rem; */
    background-color:whitesmoke;
    opacity: 80%;
    z-index: 10;
    color: var(--title-color);
}
.contact__form-area{
    height: 11rem;
}
.contact__form-area textarea{
    resize: none;
}

@media screen and (max-width:992px) {
    .contact__container{
        column-gap: 3rem;
    }
}

@media screen and (max-width:768px) {
    .contact__container{
        grid-template-columns: 1fr;
        row-gap: 3rem;
    }
    .contact__info{
        justify-content: center;
    }
    .contact__form{
        margin: 0 auto;
    }
}

@media screen and (max-width:576px) {
    .contact__info{
        grid-template-columns: 1fr;
    }
    .contact__form{
        width: 100%;
    }
}
